//For Darkmode and Lightmode
.darkMode {
    background-color: #121212;

    p,
    h1,
    h5 {
        color: $white;
    }
}
.lightMode {
    background-color: $white;

    p,
    h1,
    h5 {
        color: #000;
    }
}

//For spacing between sections
section,
header,
footer {
    margin-top: 8rem;

    @include media-breakpoint-down(md) {
        margin-top: 5rem;
    }
    @include media-breakpoint-down(xs) {
        margin-top: 4rem;
    }
}

//Header Section
header {
    @include media-breakpoint-down(xxs) {
        height: 85vh;
    }
    .intro {
        @include media-breakpoint-down(sm) {
            text-align: center;
        }

        .animate-text {
            span:before {
                content: '';
                transform: translateX(-50px);
                animation: animate 12s linear 1.5s infinite;
            }

            @keyframes animate {
                0% {
                    transform: translateY(-50px);
                    content: 'Ayush';
                }
                25% {
                    content: 'a Developer';
                }
                50% {
                    content: 'a Designer';
                }
                75% {
                    content: 'a Teacher';
                }
                100% {
                    content: 'a Learner';
                }
            }
        }
    }
    .padding__right {
        padding-right: 3rem;
        @include media-breakpoint-down(sm) {
            padding: 0 1rem;
        }
    }
    .d__reverse {
        @include media-breakpoint-down(sm) {
            flex-direction: column-reverse;
        }
    }
    .illustration {
        width: 100%;
        @include media-breakpoint-down(sm) {
            padding: 3rem;
            padding-top: 0;
        }
    }
}

//Skills Section
.skill {
    &--more {
        @include media-breakpoint-down(sm) {
            margin-top: 3rem;
        }
    }
    .p__right {
        padding-right: 6rem;
        @include media-breakpoint-down(md) {
            padding-right: 0rem;
        }
    }
}

//Contacts Section
.contact {
    .illustration2 {
        width: 100%;
        padding-right: 3rem;
        @include media-breakpoint-down(md) {
            padding-right: 0rem;
            margin-bottom: 4rem;
        }
    }

    .social {
        width: 1.75rem;
    }

    &--details {
        @include media-breakpoint-down(md) {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            p {
                text-align: center;
            }
        }
    }
}

//About-Cover
.cover {
    position: relative;
    &--image {
        height: 20rem;
        letter-spacing: 0.45rem;
    }

    .profile--picture {
        border-radius: 100%;
        height: 190px;
        width: 190px;
        border: 10px solid #3f3d56;
        background-image: url('../../images/profile.jpg');
        background-size: 184px 276px;
        position: absolute;
        left: 20%;
        top: 40%;
    }

    &--intro {
        padding-top: 112px;
        padding-left: 1.5rem;
        padding-bottom: 1.5rem;
    }
}

.about {
    margin-top: 4rem;

    &--content {
        @include b-radius;
        width: 60%;
    }
}

.experience {
    &--date {
        width: 102px;
        height: 102px;
    }

    &--description {
        @include b-radius;
        width: 372px;
    }

    &--timeline {
        width: 90px;
        height: 2px;
        border: 2px solid $work;
    }
}
